<template>
  <div>
    <v-form>
      <v-container>
        <v-text-field
          v-model="query"
          label="Search your favourite movies of 2021"
        ></v-text-field>
        <v-btn @click="submitQuery"> Search </v-btn>
      </v-container>
    </v-form>
    
    <v-row> 
        <v-col>
             <Result v-for="result in info.Search" :key="result" :result="result" v-on:addNomination="addToNominationList" />
        </v-col>
    </v-row>
    <!-- <v-alert v-if="maxFive" type="warning" >
    You've chosen your five nominations! 
  </v-alert> -->

  
    <v-row>
        <Nomination v-for="nomination in nominationList" :key="nomination" :nomination="nomination"/> 
    </v-row>

  </div>
</template>

<script>
import axios from "axios";
import Result from "./Result.vue";
import Nomination from "./Nomination.vue";
export default {
  name: "Shoppies",
  data: () => ({
    query: "",
    info: [],
    nominationList: [],
    // maxFive: false,
  }),
  methods: {
    submitQuery: function () {
      const apiKey = "cfb2c9c";
      const movieDataURL = "http://www.omdbapi.com/?apikey=" + apiKey;
      var movieName = this.query;
      // var fakeMovieName = 'Interstellar';

      axios
        .get(movieDataURL, {
          params: {
            s: movieName,
            type: "movie",
            //'y': year
          },
        })
        .then((response) => {
          console.log(response.data);
          console.log("here");
          if (response.status == 200) {
            this.info = response.data;
          }
        });
    },
    addToNominationList: function (result) {
        this.nominationList.push(result);
        console.log(this.nominationList);
        if (this.nominationList.length >= 5) {
            console.alert("You've hit your maximum!"); // change to pop up 
            // maxFive = true;
        }
    }
  },
  components: {
    Result,
    Nomination,
  },
};
</script>

